.metric {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: end;
      align-items: flex-end;
  position: absolute;
  min-width: 55px;
}

.metric-label {
  font-size: 12px;
}

.metric-amount {
  margin-top: 4px;
  font-size: 11px;
  color: var(--gray);
}

.metric-percentage {
  margin-top: 5px;
  padding: 3px;
  border-radius: 5px;
  font-size: 10px;
}

.metric-percentage.alt {
  color: black;
}



